import * as React from 'react';
import { connect } from 'react-redux';
import { Link } from 'react-router-dom';

export const Programms = connect()(
    () => {
        return <div>
             <br/>
            <Link to={`/programm/1`}> 1</Link><br/>
            <Link to={`/programm/2`}> 2</Link><br/>
            <Link to={`/programm/3`}> 3</Link><br/>
            <Link to={`/programm/4`}> 4</Link><br/>
            <Link to={`/programm/5`}> 5</Link><br/>
            <Link to={`/programm/6`}> 6</Link><br/>
            <Link to={`/programm/7`}> 7</Link>
        </div>;
    },
);



